import React from "react"
import { HUtils, MyUI, MyLoading } from "@macashipo/mlib"
import { TableHeaderColumn, BootstrapTable } from "src/libs/ReactBootstrapTable"
import {
  fnListMyPage,
  KConfigPageTable,
  kfnGetComponentTable,
  kfnGetDataTableFilterd,
} from "src/helpers/fnList"
import MUIATTableDetails from "./admintable_table_details"
import classnames from "classnames"
import styled from "styled-components"
import { FlowGetClassNameTable } from "src/helpers/Flow"
import i18next from "i18next"
import K from "src/helpers/K"
import { FlowGetMorePropsTableToShowFooter } from "src/helpers/Flows/TableFooter"
import {
  FlowGetConfigInLocalStorageOfPage,
  FlowUpdateConfigInLocalStorageOfPageWithKey,
} from "src/helpers/Flows/Page"
const WrapContainer = styled.div`
  &.a-view-grid {
    background-color: transparent;
    box-shadow: none;
    & > .card-body {
      padding: 0px;
      // overflow hidden do paging overflow xuat hien scroll
      overflow: inherit !important;
    }
  }
  ${({ fixHeightTable }) =>
    fixHeightTable &&
    `
  & > div > div > .react-bs-table {
    overflow: auto;
    max-height: 200px;
    & > .react-bs-container-header {
      position: sticky;
      top: 0;
      z-index: 1;
    }
  }
  `}
`
class MUIATTable extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      fixHeightTable: null,
    }
  }
  getClassName = (_configPageTable = {}) => {
    const { cfp } = this.props
    return classnames(
      _configPageTable[KConfigPageTable.classNameTable],
      {
        "table-has-expand": this.getHasExpand(),
      },
      FlowGetClassNameTable(cfp.fnList)
    )
  }
  getHasExpand = () => {
    const { cfp } = this.props
    let _hasExpand = fnListMyPage.fnGetConfigPageTable(cfp.fnList, [
      KConfigPageTable.hasExpand,
    ])
    // console.warn("getHasExpand:", _hasExpand)
    return _hasExpand === true
  }
  getHasPagingRemote = () => {
    const { cfp } = this.props
    let _hasPagingRemote = fnListMyPage.fnGetConfigPageTable(cfp.fnList, [
      KConfigPageTable.hasPagingRemote,
    ])
    // console.warn("getHasPagingRemote:", _hasPagingRemote)
    return _hasPagingRemote === true
  }
  getConfigSelectRow = () => {
    const { cfp } = this.props
    let _configPageTable = fnListMyPage.fnGetConfigPageTable(cfp.fnList, [])
    if (_configPageTable && _configPageTable.configSelectRow) {
      return {
        mode: "checkbox",
        // columnWidth: '60px',
        // customComponent: (props)=>{},
        // hideSelectColumn: true,
        // bgColor: 'pink',
        // selected: [ 0, 2, 4 ] ,
        // unselectable: [ 1, 3 ] ,
        // clickToSelect: true ,
        ..._configPageTable.configSelectRow,
      }
    }
    return null
  }
  getOptionsTable = () => {
    const { cfp } = this.props
    const data = this.getData()
    let _configPageTable = fnListMyPage.fnGetConfigPageTable(cfp.fnList, [])
    let _optionsTable = {
      sortIndicator: true,
      hideSizePerPage: false,
      withFirstAndLast: true,
      paginationShowsTotal: true,
      hidePageListOnlyOnePage: true,
      onPageChange: (page, sizePerPage) => {
        console.log("onPageChange", page, sizePerPage)
        cfp.onePage._configPagination._pageSize = sizePerPage
        cfp.onePage._configPagination.go(page) //will call onePage._configPagination.afterChange
        FlowUpdateConfigInLocalStorageOfPageWithKey(
          cfp.configPageExt,
          "sizePerPage",
          sizePerPage
        )
        // console.log("onPageChange 2", cfp.onePage._configPagination)
      },
      onSortChange: (sortName, sortOrder) => {
        console.log("onSortChange", sortName, sortOrder)
        cfp.onePage._oneFilters.updateCurrentSort(sortName, sortOrder) //will call onePage._oneFilters.afterChangeSort
      },
      ..._configPageTable.optionsTable,
      page: cfp.onePage._configPagination._pageCurrent, //page va pageSize lay tu onePage, da dc update tu configPageTable
      sizePerPage: cfp.onePage._configPagination._pageSize,
    }
    if (this.getHasExpand()) {
      _optionsTable = {
        ..._optionsTable,
        ...{
          expandAllChilds: true,
          expandRowBgColor: "white",
          expandBy: "column",
          expandBodyClass: "react-bs-container-row-expand", //this._getTableConfig("expandBodyClass",'react-bs-container-row-expand'),
          expanding: [], //this.getExpanding(),
        },
      }
      if (data && data.length > 0 && _optionsTable.expanding) {
        for (let i = 0; i < data.length; i++) {
          _optionsTable.expanding.push(data[i].Id)
        }
      }
    }

    console.log(
      "_optionsTable",
      _configPageTable,
      _optionsTable,
      cfp.onePage._configPagination,
      _configPageTable.optionsTable
    )
    return _optionsTable
  }
  getData = () => {
    const { cfp } = this.props
    return fnListMyPage.getDataList(cfp.fnList, [])
  }
  updateOffsetTopOfTable = (offsetTopOfTable = 0) => {
    console.log("updateOffsetTopOfTable:", offsetTopOfTable)
    const { cfp } = this.props
    let _configPageTable = fnListMyPage.fnGetConfigPageTable(cfp.fnList, [])
    if (_configPageTable[K.KConfigPageTable.fixHeightScrollTable] != null) {
      let _height =
        document.body.offsetHeight - offsetTopOfTable - 40 - 30 - 100
      this.setState({
        fixHeightTable: _height,
      })
    }
  }
  getShowPagination = () => {
    const { cfp } = this.props
    const { onePage } = cfp
    let _showPagination = true
    if (onePage["UI_IsInDetail"] === true && !this.getHasPagingRemote()) {
      _showPagination = false
    }
    return _showPagination
  }
  getMoreProps = (moreProps = {}) => {
    const { cfp } = this.props
    const { onePage } = cfp
    let _moreProps = moreProps || {}
    let _selectRow = this.getConfigSelectRow()
    if (this.getHasExpand()) {
      _moreProps = {
        ..._moreProps,
        ...{
          expandableRow: () => {
            return true
          },
          expandComponent: this.renderExpand,
        },
      }
    }
    if (this.getHasPagingRemote() && onePage && onePage._configPagination) {
      _moreProps = {
        ..._moreProps,
        ...{
          remote: true,
          fetchInfo: {
            dataTotalSize: onePage._configPagination._totalItems,
          },
        },
      }
    }
    if (_selectRow) {
      _moreProps.selectRow = _selectRow
    }
    //bo sung footer
    let _morePropsForFooter = FlowGetMorePropsTableToShowFooter({
      fnList: cfp.fnList,
    })
    if (_morePropsForFooter && _morePropsForFooter.footer) {
      _moreProps = {
        ..._moreProps,
        ..._morePropsForFooter,
      }
    }
    // console.error("moreProps table:", _moreProps)
    return _moreProps
  }
  renderExpand = row => {
    // console.warn("renderExpand row", row)
    return <MyUI {...this.props} row={row} type="admintable_table_details" />
  }
  renderTableCols = () => {
    const { cfp } = this.props
    // let _cols = HUtils.runFuntion(this.props.cfp, "getTableCols", [])
    let _cols = fnListMyPage.fnGetTableCols(cfp.fnList)
    let _configPageTable = fnListMyPage.fnGetConfigPageTable(cfp.fnList, [])
    console.warn("renderTableCols", _cols, _configPageTable)
    let _ui = []
    if (_cols && _cols.length > 0) {
      //truong hop chi co 1 cot la ID, thi cung show no columns to show
      if (_cols.length == 1 && _cols[0].hidden === true) {
        return []
      }
      let _defaultCol = {
        columnClassName: "",
        className: "",
        canSort:
          _configPageTable.canSort != null ? _configPageTable.canSort : true,
      }
      for (let i = 0; i < _cols.length; i++) {
        let _itemCol = _cols[i]
        let _moreProps = {}
        let _header = _itemCol.header
        if (_itemCol.colProps != null) {
          _moreProps = Object.assign(_moreProps, _itemCol.colProps)
        }
        if (_moreProps.componentHeader != null) {
          _header = _moreProps.componentHeader(
            _itemCol.header,
            _itemCol.dataField
          )
        }
        _ui.push(
          <TableHeaderColumn
            key={i}
            columnClassName={
              _itemCol.columnClassName || _defaultCol.columnClassName
            }
            className={_itemCol.className || _defaultCol.className}
            width={_itemCol.width || "100px"}
            isKey={_itemCol.isKey}
            hidden={_itemCol.hidden}
            editable={false}
            dataField={_itemCol.dataField || ""}
            dataAlign={_itemCol.dataAlign || "left"}
            filter={null}
            // formatExtraData={{ fieldName: dataField }}//dung trong _moreProps
            dataFormat={_itemCol.dataFormat}
            expandable={false}
            dataSort={
              _itemCol.dataSort != null
                ? _itemCol.dataSort
                : _defaultCol.canSort
            }
            {..._moreProps}
          >
            {_header}
          </TableHeaderColumn>
        )
      }
      return _ui
    } else if (_cols && _cols.length == 0) {
      return []
    }
    //chua co
    return null
  }
  render() {
    const { cfp } = this.props
    const uiCols = this.renderTableCols()
    let _currentSwitchView = fnListMyPage.fnGetCurrentSwitchView(cfp.fnList, [])
    console.info(
      "render ui table:",
      uiCols,
      cfp.countReloadList,
      this.getOptionsTable()
    )
    if (uiCols && uiCols.length > 0) {
      let _configPageTable =
        fnListMyPage.fnGetConfigPageTable(cfp.fnList, []) || {}
      return (
        <WrapContainer
          className={classnames(
            "mpat-table mb-3 card",
            `a-view-${_currentSwitchView.type || "table"}`
          )}
          fixHeightTable={this.state.fixHeightTable}
          ref={r => {
            if (r) {
              console.log(r, r.offsetTop)
              this.updateOffsetTopOfTable(r.offsetTop)
            }
          }}
        >
          <div className="card-body" style={{ overflow: "auto" }}>
            <BootstrapTable
              key={cfp.countReloadList}
              className={this.getClassName(_configPageTable)}
              customRender={({ data }) => {
                if (_currentSwitchView && _currentSwitchView.type == "grid") {
                  return (
                    <MyUI
                      {...this.props}
                      type="admintable_table_grid_card"
                      data={data}
                      cfp={cfp}
                    />
                  )
                }
                return null
              }}
              ref={r => {
                this._cTable = r
                HUtils.runFuntion(cfp, "updateComponentTable", [r])
                //update fnList
                if (this._cTable) {
                  cfp.fnList[kfnGetDataTableFilterd] = () => {
                    if (this._cTable.getTableDataIgnorePaging) {
                      return this._cTable.getTableDataIgnorePaging()
                    }
                    return []
                  }
                  cfp.fnList[kfnGetComponentTable] = () => {
                    return this._cTable
                  }
                  cfp.fnList["fnTableCleanSort"] = () => {
                    if (this._cTable && this._cTable.cleanSort) {
                      this._cTable.cleanSort()
                    }
                  }
                }
              }}
              style={{ width: "1000px" }}
              data={this.getData()}
              version="4"
              pagination={this.getShowPagination()}
              search={false}
              hover={true}
              striped={false}
              options={this.getOptionsTable()}
              insertRow={false}
              trClassName={(data, r) => {
                let _classRow = ""
                if (data && data.IsDeleted === true) {
                  //&& data.IsDelete===true
                  _classRow += " mt-rdeleted"
                }
                // console.warn("row:,data",data,r);
                return _classRow
              }}
              {...this.getMoreProps(
                _configPageTable[KConfigPageTable.tableProps]
              )}
            >
              {uiCols}
            </BootstrapTable>
          </div>
        </WrapContainer>
      )
    } else {
      if (uiCols && uiCols.length == 0) {
        return (
          <div
            style={{
              width: "100%",
              textAlign: "center",
            }}
          >
            {i18next.t("No table columns to show")}
          </div>
        )
      }
      return (
        <div
          style={{
            width: "100%",
            textAlign: "center",
          }}
        >
          <MyLoading />
        </div>
      )
    }
  }
}

export { MUIATTableDetails }
export default MUIATTable
