import { HUtils, MyPage } from "@macashipo/mlib"
import React, { useState } from "react"
import useForceUpdate from "../../hooks/useForceUpdate"
import V from "src/helpers/V"
import { FlowGetAllExpandItemOfRow } from "src/helpers/Flows/TableExpand"
function MUIATTableDetailOne(props) {
  const { row, configExpand } = props
  console.warn("configExpand:", configExpand)
  let _requestData = {}
  if (configExpand.queryFromParent) {
    for (let k of Object.keys(configExpand.queryFromParent)) {
      _requestData[k] = row[configExpand.queryFromParent[k]]
    }
  }
  return (
    <div className="mpat-detail-item" style={{ minHeight: "0px" }}>
      <div className="mpat-detail-arrow">
        <i className="fa fa-angle-double-right"></i>
      </div>
      <MyPage
        configPage={{
          APIName: configExpand.controller,
          Config: null,
          Id: `expand-${configExpand.screenCode}-${row.Id}`,
          RequestData: _requestData,
          ScreenCode: configExpand.screenCode,
          Title: configExpand.title || row[configExpand.titleField] || "",
          UIType: "admin_table",
        }}
        fnList={{
          fnGetIsInTableExpand: () => {
            return true
          },
        }}
      />
    </div>
  )
}
export default function MUIATTableDetails(props) {
  const { row, cfp } = props
  const [keyTime, setKeyTime] = useState(new Date().getTime())
  const forceUpdate = useForceUpdate()
  const allExpand = FlowGetAllExpandItemOfRow({ row, onePage: cfp.onePage })
  row["_fnForceUpdateDetail"] = () => {
    console.warn("_fnForceUpdateDetail:", props, keyTime)
    // forceUpdate()
    setKeyTime(new Date().getTime())
  }
  // console.warn("render Details:", props, row, allExpand)
  return (
    <div className="mpat-detail" style={{ minHeight: "0px" }} key={keyTime}>
      {/* Detail: */}
      {allExpand.length > 0 &&
        allExpand.map((v, i) => {
          let _isShow = V.TableExpand_IsShowExpandItem({
            row,
            configItemExpand: v,
            prefix: v._prefix,
          })
          // console.log("expand isshow:", _isShow, v)
          // if (_isShow) return <div>{v.screenCode}</div>
          if (_isShow)
            return <MUIATTableDetailOne key={i} {...props} configExpand={v} />
          else return null
        })}
    </div>
  )
}
