/**
 *
 * 1. Check Screen Config Table.showTotal == true
 * 2. Add props table footer = true, footerData = data
 *
 */

import { fnListMyPage } from "../fnList"
import { KConfigApp, KConfigPageTable } from "../K"
import { HCF, HFormat } from "@macashipo/mlib"

export const FlowGetMorePropsTableToShowFooter = ({ fnList }) => {
  let _more = {}
  if (fnList) {
    let _showTotal = fnListMyPage.fnGetConfigPageTable(fnList, [
      KConfigPageTable.showTotal,
    ])
    let _footerData = FlowGetFooterData({ fnList })
    if (_showTotal === true && _footerData) {
      _more = {
        footer: true,
        footerData: _footerData,
      }
    }
  }
  return _more
}

export const FlowGetFooterData = ({ fnList } = {}) => {
  let _cols = fnListMyPage.fnGetTableCols(fnList)
  // console.warn("FlowGetFooterData cols:", _cols)
  if (_cols && _cols.length > 0) {
    let _configTotal =
      fnListMyPage.fnGetConfigPageTable(fnList, [
        KConfigPageTable.configTotal,
      ]) || {}
    let _defaultAlign = "right"
    let _defaultFormatNumber = HCF.getCF(KConfigApp.numeralFormat)
    let _configColTotal = {}
    let _prefix = null
    let _suffix = null

    if (_configTotal.defaultAlign) {
      _defaultAlign = _configTotal.defaultAlign
    }
    if (_configTotal.defaultFormatNumber) {
      _defaultFormatNumber = _configTotal.defaultFormatNumber
    }
    if (_configTotal.configColTotal) {
      _configColTotal = _configTotal.configColTotal
    }
    if (_configTotal.prefix != null) {
      _prefix = _configTotal.prefix
    }
    if (_configTotal.suffix != null) {
      _suffix = _configTotal.suffix
    }
    let _styleAllCol = {}
    if (_configTotal.styleAllCol) {
      _styleAllCol = _configTotal.styleAllCol
    }

    let _data = []
    let _dataTotal = fnListMyPage.fnGetDataTotal(fnList, [])
    if (_dataTotal && _dataTotal.length > 0) {
      for (let i in _dataTotal) {
        let _item = _dataTotal[i]
        let _dataItem = []
        let _keyOfItem = Object.keys(_item)
        for (let ki = 0; ki < _keyOfItem.length; ki++) {
          let _keyKI = _keyOfItem[ki]
          let _index = 0

          for (let kc = 0; kc < _cols.length; kc++) {
            let _keyKC = _cols[kc].dataField
            if (_keyKI == _keyKC) {
              _index = kc
              break
            }
          }
          let _colConfig = _configColTotal[_keyKI] || {}
          let _styleOfCol = _colConfig.style || {}
          let _colPrefix = _prefix
          let _colSuffix = _suffix
          if (_colConfig.prefix != null) {
            _colPrefix = _colConfig.prefix
          }
          if (_colConfig.suffix != null) {
            _colSuffix = _colConfig.suffix
          }
          _dataItem.push({
            label: _item[_keyKI],
            columnIndex: _index,
            align: _colConfig.align != null ? _colConfig.align : _defaultAlign,
            formatter: tableData => {
              let _value = _item[_keyKI]
              if (_colConfig.fShow) {
                _value = _item[_colConfig.fShow]
              }
              if (typeof _item[_keyKI] == "number") {
                let _format = _colConfig.format || _defaultFormatNumber
                if (typeof _format == "string") {
                  _value = HFormat.format(_value, {
                    type: "numeral",
                    format: _format,
                  })
                } else if (_format && _format.type) {
                  _value = HFormat.format(_value, {
                    ..._format,
                  })
                }
              }
              if (
                (typeof _value === "string" || typeof _value === "number") &&
                _colConfig.isHide !== true
              ) {
                return (
                  <div
                    title={_value}
                    style={{ ..._styleAllCol, ..._styleOfCol }}
                  >
                    {_colPrefix != null && _colPrefix}
                    {_value}
                    {_colSuffix != null && _colSuffix}
                  </div>
                )
              }
              return (
                <div style={{ ..._styleAllCol, ..._styleOfCol }}>&nbsp;</div>
              )
            },
          })
        }
        _data.push(_dataItem)
      }
    }
    return _data
  }
}
